import { make } from 'vuex-pathify'
// import * as api from "@/apis/webapi"

const state = {
    // 用户信息
    upexconfig:{},
    uptimconfig:{},
    updtmconfig:{},
    upbpconfig:{},
}

const mutations = make.mutations(state)

const actions = {
    ...make.actions('upexconfig'),
    ...make.actions('uptimconfig'),
    ...make.actions('upbpconfig'),
    ...make.actions('updtmconfig'),
    // 更新DBM策略
    update_upexconfig({dispatch},payload){
        dispatch("setUpexconfig",payload);
    },
    // 更新TIM策略
    update_uptimconfig({dispatch},payload){
        dispatch("setUptimconfig",payload);
    },
    update_updtmconfig({dispatch},payload){
        dispatch("setUpdtmconfig",payload);
    },
    update_upbpconfig({dispatch},payload){
        dispatch("setUpbpconfig",payload);
    },

}

const getters = {
    
    
        
    
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}
