import Vue from 'vue'
import Router from 'vue-router'
import goTo from 'vuetify/es5/services/goto'

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    // This is for the scroll top when click on any router link
    scrollBehavior: (to, from, savedPosition) => {
        let scrollTo = 0

        if (to.hash) {
            scrollTo = to.hash
        } else if (savedPosition) {
            scrollTo = savedPosition.y
        }

        return goTo(scrollTo)
    },
    // This is for the scroll top when click on any router link
    routes: [
        {
            path: '/',
            // redirect: '#',
            component: () => import('@/layouts/full-layout/Layout'),
            children: [
                {
                    name: 'wellcome',
                    path: '/',
                    component: () => import('@/views/dbm/dbmwellcome'),
                },
                {
                    name: 'dbm',
                    path: 'dbm/wellcome',
                    component: () => import('@/views/dbm/dbmwellcome'),
                },
                {
                    name: 'user',
                    path: 'user/userinfo',
                    component: () => import('@/views/authentication/UserInfo'),
                },
                {
                    name: 'dbm',
                    path: 'dbm/panel',
                    component: () => import('@/views/dbm/dbmpanel'),
                },
                {
                    name: 'dbm',
                    path: 'dbm/exconfig',
                    component: () => import('@/views/dbm/dbmExchangeApi'),
                },
                {
                    name: 'dbm',
                    path: 'dbm/addconfig',
                    component: () => import('@/views/dbm/dbmaddConfig'),
                },
                {
                    name: 'dbm',
                    path: 'dbm/updateconfig',
                    component: () => import('@/views/dbm/dbmupdateconfig'),
                },
                {
                    name: 'dbm',
                    path: 'dbm/profit',
                    component: () => import('@/views/dbm/dbmprofit'),
                },
                {
                    name: 'dbm',
                    path: 'dbm/renewal',
                    component: () => import('@/views/dbm/dbmselfrenewal'),
                },

                // 定投管理
                {
                    name: 'tim',
                    path: 'tim/addconfig',
                    component: () => import('@/views/tim/timaddconfig'),
                },
                {
                    name: 'tim',
                    path: 'tim/updateconfig',
                    component: () => import('@/views/tim/timupdateconfig'),
                },
                {
                    name: 'tim',
                    path: 'tim/panel',
                    component: () => import('@/views/tim/timpanel'),
                },
                {
                    name: 'dtm',
                    path: 'dtm/addconfig',
                    component: () => import('@/views/dtm/dtmaddconfig'),
                },
                {
                    name: 'dtm',
                    path: 'dtm/updateconfig',
                    component: () => import('@/views/dtm/dtmupdateconfig'),
                },
                {
                    name: 'dtm',
                    path: 'dtm/panel',
                    component: () => import('@/views/dtm/dtmpanel'),
                },
                // 智能搬砖
                {
                    name: 'bp',
                    path: 'bp/addconfig',
                    component: () => import('@/views/tim/timaddconfig'),
                },
                {
                    name: 'bp',
                    path: 'bp/updateconfig',
                    component: () => import('@/views/tim/timupdateconfig'),
                },
                {
                    name: 'bp',
                    path: 'bp/panel',
                    component: () => import('@/views/brickpicker/bpPanel'),
                },
                // DbManager
                {
                    name: 'dbmanager',
                    path: 'dbmanager/dbview',
                    component: () => import('@/views/dbmanager/dbview/DbView'),
                },
                {
                    name: 'dbmanager',
                    path: 'dbmanager/upload',
                    component: () => import('@/views/dbmanager/dbview/UploadView'),
                },
                {
                    name: 'dbmanager',
                    path: 'dbmanager/config',
                    component: () => import('@/views/dbmanager/dbview/DbConfig'),
                },
                // 管理员
                {
                    name: 'admin',
                    path: 'admin/transfer',
                    component: () => import('@/views/admin/AdminTrxTransfer'),
                },
                {
                    name: 'admin',
                    path: 'admin/encode',
                    component: () => import('@/views/admin/AdminEncode'),
                },
                {
                    name: 'admin',
                    path: 'admin/exbotactive',
                    component: () => import('@/views/admin/AdminExbotActive'),
                },
                {
                    name: 'admin',
                    path: 'admin/dbmorders',
                    component: () => import('@/views/admin/AdminDbmOrders'),
                }, 
                {
                    name: 'admin',
                    path: 'admin/userbalance',
                    component: () => import('@/views/admin/AdminGetUserBalance'),
                },
                {
                    name: 'admin',
                    path: 'admin/adminlogin',
                    component: () => import('@/views/authentication/BoxedLoginHash'),
                },
                // trx 链上管理
                {
                    name: 'trx',
                    path: 'trx/transfer',
                    component: () => import('@/views/admin/AdminTrxTransfer'),
                },
                {
                    name: 'trx',
                    path: 'trx/trc10history',
                    component: () => import('@/views/trx/Trc10History'),
                },
                {
                    name: 'trx',
                    path: 'trx/trc10holders',
                    component: () => import('@/views/trx/TrxToken'),
                },
                
                
                
                

            ]
        },
        {
            path: '/user',
            component: () => import('@/layouts/blank-layout/Blanklayout'),
            children: [
                // 用户登陆、注册
                {
                    name: 'user',
                    path: 'login',
                    component: () => import('@/views/authentication/BoxedLogin'),
                },
                
                {
                    name: 'user',
                    path: 'register',
                    component: () => import('@/views/authentication/BoxedRegister'),
                },
                {
                    name: 'user',
                    path: 'resetpassword',
                    component: () => import('@/views/authentication/BoxedResetpassword'),
                },
                {
                    name: 'user',
                    path: 'logout',
                    component: () => import('@/views/authentication/Logout'),
                },
                

            ]
        },
        {
            path: '/pemanager',
            component: () => import('@/layouts/full-layout/Layout'),
            children: [
                // 开发指标分析
                {
                    name: 'peProjectManager',
                    path: 'projectManager',
                    component: () => import('@/views/pemanager/ProjectManager/ProjectManager'),
                },
                // 开发指标分析
                {
                    name: 'developIndexAnalysis',
                    path: 'developIndexAnalysis',
                    component: () => import('@/views/pemanager/DevelopIndexAnalysis/DevelopIndexAnalysis'),
                },
                // 开发指标分析
                {
                    name: 'DevelopIndexPrediction',
                    path: 'developIndexPrediction',
                    component: () => import('@/views/pemanager/DevelopIndexPrediction/DevelopIndexPrediction'),
                },
                // 开发指标分析
                {
                    name: 'ProdApproval',
                    path: 'prodApproval',
                    component: () => import('@/views/pemanager/ProdApproval/ProdApproval'),
                },
                // 开发指标分析
                {
                    name: 'ProductionRunAnalysis',
                    path: 'productionRunAnalysis',
                    component: () => import('@/views/pemanager/ProductionRunAnalysis/ProductionRunAnalysis'),
                },
                // 开发指标分析
                {
                    name: 'RecoverableReservesEvaluation',
                    path: 'recoverableReservesEvaluation',
                    component: () => import('@/views/pemanager/RecoverableReservesEvaluation/RecoverableReservesEvaluation'),
                },
                // 开发指标分析
                {
                    name: 'WaterFloodAnalysis',
                    path: 'WaterFloodAnalysis',
                    component: () => import('@/views/pemanager/WaterFloodAnalysis/WaterFloodAnalysis'),
                },
            ]
        },

        {
            path: '/authentication',
            component: () => import('@/layouts/blank-layout/Blanklayout'),
            children: [
                {
                    name: 'BoxedLogin',
                    path: 'boxedlogin',
                    component: () => import('@/views/authentication/BoxedLogin'),
                },
                // 用户登陆、注册
                {
                    name: 'UserLogin',
                    path: 'user/login',
                    component: () => import('@/views/authentication/BoxedLogin'),
                },
               
                {
                    name: 'FullLogin',
                    path: 'fulllogin',
                    component: () => import('@/views/authentication/FullLogin'),
                },
                {
                    name: 'BoxedRegister',
                    path: 'boxedregister',
                    component: () => import('@/views/authentication/BoxedRegister'),
                },
                {
                    name: 'FullRegister',
                    path: 'fullregister',
                    component: () => import('@/views/authentication/FullRegister'),
                },
                {
                    name: 'Error',
                    path: 'error',
                    component: () => import('@/views/authentication/Error'),
                },
            ]
        },
        
        

        {
            path: '*',
            component: () => import('@/views/authentication/Error'),
        }
    ],
})