// 用户状态管理中心


import { make } from 'vuex-pathify'
// import * as api from "@/apis/webapi"
import * as api from "@/apis/wswebapi"

const state = {
    islogin:false,
    // 用户信息
    userinfo:{},
    trxaddress:"",
    trxhistoryaddress:"",
    exbotexpiretime:0,
    renewaltime:0,
}

const mutations = make.mutations(state)

const actions = {
    ...make.actions('userinfo'),
    ...make.actions('islogin'),
    ...make.actions('trxaddress'),
    ...make.actions('trxhistoryaddress'),
    ...make.actions('testlogin'),
    ...make.actions('exbotexpiretime'),
    ...make.actions('renewaltime'),

    // 更新用户登陆状态
    update_loginstatus({dispatch}){
        api.GetLoginStatus().then(res=>{
            if (res.code==200){
                console.log("更新登陆状态成功",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
            }else{
                console.log("更新登陆状态失败",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // 更新用户信息
    update_userinfo({dispatch}){
        api.GetUserInfo().then(res=>{
            if (res.code==200){
                console.log("更新用户信息成功",res);
                // dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                dispatch("setUserinfo",res.data);
                dispatch("setIslogin",true);
            }else{
                console.log("更新用户信息失败",res);
                // dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
                dispatch("setIslogin",false);
            }
        })
    },
    // 登陆
    login({dispatch},payload){
        api.Login(payload).then(res=>{
            if (res.code==200){
                console.log("登陆成功",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                dispatch("update_userinfo");
                dispatch("setIslogin",true);
                dispatch("update_trxaddress");
                dispatch("peproject/update_db_blocknames",{"msg":"更新区块名"},{root:true});
                dispatch("update_exbot_expiretime");
            }else{
                console.log("登陆失败",res);
                dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"},{root:true})
            }
        })
        // 登陆本地客户端
        api.LocalDbLogin(payload);
    },
    // 登出
    logout({dispatch}){
        api.Logout();
        dispatch("setIslogin",false);
        dispatch("setUserinfo",{});
   
    },
    // 发送邮箱验证码
    send_email_code({dispatch},payload){
        api.GetEmailcode(payload).then(res=>{
            if (res.code==200){
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
            }else{
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true});
            }
        })
    },
    // 注册事件
    register({dispatch},payload){
        api.Register(payload).then(res=>{
            if (res.code==200){
                console.log("注册成功",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                dispatch("setIslogin",true);
            }else{
                console.log("注册失败",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // 重置密码事件
    resetpassword({dispatch},payload){
        api.ResetPassword(payload).then(res=>{
            if (res.code==200){
                console.log("重置密码成功",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                dispatch("setIslogin",true);
            }else{
                console.log("重置密码失败",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // 更新用户trxaddress
    update_trxaddress({dispatch}){
        api.GetTrxAddress().then(res=>{
            if (res.code==200){
                dispatch("setTrxaddress",res.data);
                dispatch("setTrxhistoryaddress",res.data);
            }else{
                console.log(res);
            }
        })
    },
    // 更新trxhistoryaddress
    update_trxhistoryaddress({dispatch},payload){
        dispatch("setTrxhistoryaddress",payload);
    },
    // exbot过期时间
    update_exbot_expiretime({dispatch}){
        api.DBMExpireTime().then(res=>{
            if (res.code==200){
                dispatch("setExbotexpiretime",res.data);
            }else{
                console.log(res);
                dispatch("setExbotexpiretime",0);
            }
        })
    },
    // 更新续费exbot时间
    update_renewaltime({dispatch}){
        dispatch("setRenewaltime",new Date().getTime());
    }
}

const getters = {
    // 获取集合
    collections:state=>{
        if (state.userinfo.collectionname){
            let collectionname=state.userinfo.collectionname;
            let keys=Object.keys(collectionname);
            let cols =keys.map(key=>({
                text:collectionname[key],value:key,
            }))
            return cols;
        }else{
            return [];
        }
    },
    username:state=>{
        return state.userinfo.username;
    },
    email:state=>{
        return state.userinfo.email;
    },
    trxaddress:state=>{
        return state.trxaddress;
    },
    trxaddressqrcode:state=>{
        return "https://cdn.oldking.club:2096/v1/web/generateQrcode?text="+state.trxaddress;
    },
    userauthgrade:state=>{
        if (state.userinfo.collectionauth){
            return state.userinfo.collectionauth["user"];
        }else{
            return 0
        }
        
    },
    exbotisexpire:state=>{
        let date = new Date();
        return date.getTime()>state.exbotexpiretime*1000
    },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}
