// 数据库管理数据中心

import { make } from 'vuex-pathify'
import * as api from "@/apis/wswebapi"


const state = {
    // 当前区块
    blockname:"",
    // 选择的井
    wellname:"",
    oilwellnames:[],
    waterwellnames:[],
    // 数据库中的区块
    db_blocknames:[],
    // 已经下载过的区块数据
    downloaded_blocknames:[],
    oil_day_report_status:false,
    oil_month_report_status:false,
    water_day_report_status:false,
    water_month_report_status:false,

    // 月产量曲线
    month_prod_line_data:[],
    // 月产量曲线累产
    month_prod_line_cusum_data:[],
    // 油水井数
    oil_water_wellnum_data:[],
    // 油水产能水平
    prod_injf_avg_data:[],
    // 产量构成数据
    prod_comp_data:[],
    // 注水构成数据
    inje_comp_data:[],

}

const mutations = make.mutations(state)

const actions = {
    ...make.actions('downloaded_blocknames'),
    ...make.actions('db_blocknames'),
    ...make.actions('oilwellnames'),
    ...make.actions('waterwellnames'),
    ...make.actions('blockname'),
    ...make.actions('oil_day_report_status'),
    ...make.actions('oil_month_report_status'),
    ...make.actions('water_day_report_status'),
    ...make.actions('water_month_report_status'),
    ...make.actions('month_prod_line_data'),
    ...make.actions('month_prod_line_cusum_data'),
    // 更新数据库中的区块 以月报为准
    update_db_blocknames({dispatch},payload){
        console.log(payload);
        api.GetFieldValues("oilreportmonth",{size:1000,field:"blockname"}).then(res=>{
            if (res.code==200){
                dispatch("setDb_blocknames",res.data);
            }else{
                console.log(res)
            }
        })
    },
    // 刷新状态
    update_blockname({dispatch},payload){
        dispatch("setBlockname",payload);
        console.log({blockname:payload,collection:"oilreportmonth"});
        api.CheckCsvExist({blockname:payload,collection:"oilreportmonth"}).then(res=>{
            dispatch("setOil_month_report_status",res.data);
        });
        api.CheckCsvExist({blockname:payload,collection:"waterreportmonth"}).then(res=>{
            dispatch("setWater_month_report_status",res.data);
        });
        api.CheckCsvExist({blockname:payload,collection:"oilreportday"}).then(res=>{
            dispatch("setOil_day_report_status",res.data);
        });
        api.CheckCsvExist({blockname:payload,collection:"waterreportday"}).then(res=>{
            dispatch("setWater_day_report_status",res.data);
        });

        // 更新井数据
        api.GetFieldValues("oilreportmonth",{size:1000,field:"wellname",query:"blockname:"+state.blockname+":str:"}).then(res=>{
            if (res.code==200){
                dispatch("setOilwellnames",res.data);
            }else{
                console.log(res)
            }
        })
        api.GetFieldValues("waterreportmonth",{size:1000,field:"wellname",query:"blockname:"+state.blockname+":str:"}).then(res=>{
            if (res.code==200){
                dispatch("setWaterwellnames",res.data);
            }else{
                console.log(res)
            }
        })
    },

    // 刷新开发曲线数据
    update_prod_line_data({dispatch}){
        api.GetProdLineData({blockname:state.blockname,collection:"oilreportmonth",wellname:state.wellname}).then(res=>{
            if (res.code==200){
                dispatch("setMonth_prod_line_data",res.data);
            }else{
                dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"},{root:true});
            }
        })
    },
    update_prod_line_cusum_data({dispatch}){
        api.GetProdLineCusumData({blockname:state.blockname,collection:"oilreportmonth",wellname:state.wellname}).then(res=>{
            if (res.code==200){
                dispatch("setMonth_prod_line_cusum_data",res.data);
            }else{
                dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"},{root:true});
            }
        })
    },

}
const getters = {
    ...make.getters(state),
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters,
  }
  