<template>
  <v-app id="materialpro" :class="`${!$vuetify.breakpoint.smAndDown ? 'full-sidebar' : 'mini-sidebar'}`">
      <router-view />
      <v-snackbar
      v-model="msg_needshow"
      :bottom="y === 'bottom'"
      :color="msg_color"
      :left="x === 'left'"
      :multi-line="mode === 'multi-line'"
      :right="x === 'right'"
      :timeout="msg_timeout"
      :top="y === 'top'"
      :vertical="mode === 'vertical'"
      >
      {{msg_text}}
      <v-btn
          dark
          text
          @click="close_snarckbar"
      >
          关闭
      </v-btn>
      </v-snackbar>
  </v-app>

</template>

<script>
import { sync,get } from 'vuex-pathify'
export default {
  name: 'App',

  components: {
  },
  data:()=>({
        x: null,
        y: 'top',
        top: false,
        right: true,
        bottom: true,
        left: false,
        mode: '',
    }),
    computed:{
        msg_color:get('msglog/msg_color'),
        msg_text:get('msglog/msg_text'),
        msg_timeout:sync('msglog/msg_timeout'),
        msg_needshow:sync('msglog/msg_needshow'),
        isLogin:get('user/islogin'),
    },
    methods:{
        close_snarckbar(){
            this.$store.dispatch('msglog/update_msg_needshow',false);
        },
        // 更新登陆状态
        update_login_status(){
            this.$store.dispatch('user/update_userinfo');
        },
    },
    mounted() {
        let _this = this;
        window.onresize = function(){ // 定义窗口大小变更通知事件
            _this.$store.dispatch('app/update_screenWidth',document.documentElement.clientWidth); //窗口宽度
        };
    },
    created(){
        setInterval(this.update_login_status,60000);
    },
    watch:{
        isLogin(){
            if (!this.isLogin){
                this.$router.push({path:"/user/login"});
            }
        }
    }
};
</script>
